<template>
    <div>
        <div class="row">
            <div class="col-12 p-3">
                <div class="content-wrapper">
                    <p class="section-title clearfix mb-4">
                        Edit Administrator
                        <a class="btn btn-primary btn-sm float-right" :href="URL.base + '/admin'"><i class="fa fa-arrow-left mr-2"></i>Back</a>
                    </p>
                    <form :action="URL.base + '/update-admin'" method="POST">
                        <input type="hidden" name="_token" id="_token" :value="token">
                        <input type="hidden" name="id" :value="admin.id">
                        <div class="row">
                            <div class="offset-md-2 col-md-8">
                                <div :class="['alert', 'alert-dismissible', message.status == 'success' ? 'alert-success' : 'alert-danger']" v-if="message">
                                    <button type="button" class="close" data-dismiss="alert">&times;</button>
                                    <div v-for="msg in message.body">
                                        {{msg}}
                                    </div>
                                </div>
                                <div class="row">
                                    <div class="form-group col-md-6">
                                        <label for="name"><strong>Name</strong></label>
                                        <input v-model="adminInfo.name" type="text" class="form-control" id="name" name="name" placeholder="Name..." required="required">
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="email"><strong>Email</strong></label>
                                        <input v-model="adminInfo.email" type="email" class="form-control" id="email" name="email" placeholder="E-mail..." required="required">
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="license"><strong>New Password</strong></label>
                                        <input v-model="adminInfo.newPassword" type="password" class="form-control" id="password" name="password" placeholder="New Password..." required="required" ref="password">
                                    </div>
                                    <div class="form-group col-md-6">
                                        <label for="license"><strong>Confirm Password</strong></label>
                                        <input v-model="adminInfo.confirmPassword" type="password" class="form-control" id="password_confirmation" name="password_confirmation" placeholder="Confirm Password..." required="required">
                                    </div>
                                    <div class="form-group col-md-12">
                                        <label for="parameters"><strong>Parameters</strong></label>
                                        <multiselect v-model="adminInfo.parameters" placeholder="Select Parameters" label="name" track-by="id" :options="parameterList" :multiple="true" :taggable="true"></multiselect>
                                        <input type="hidden" name="parameters" :value="getIDs(adminInfo.parameters)">
                                    </div>
                                </div>
                                <button type="submit" class="btn btn-primary float-right btn-md mt-3">Update Administrator</button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        props: ['message', 'oldInput', 'admin', 'parameterList'],

        data() {
            return {
                URL: window.URL,
                token: window.token,
                adminInfo: {
                    name: this.oldInput && this.oldInput.name ? this.oldInput.name : this.admin.name,
                    email: this.oldInput && this.oldInput.email ? this.oldInput.email : this.admin.email,
                    newPassword: null,
                    confirmPassword: null,
                    parameters: this.oldInput && this.oldInput.parameters ? this.oldInput.parameters.split(',') : this.admin.parameters
                }
            }
        },
        computed: {
        },
        methods:{
            getIDs: function(params) {
                if(Array.isArray(params))
                    return params.map(param => param.id).join(',');
                return '';
            }
        },
        created: function(){
            if(Array.isArray(this.adminInfo.parameters)) {
                for(var i = 0; i < this.adminInfo.parameters.length; i ++) {
                    this.adminInfo.parameters[i] = this.parameterList.find(x => x.id == this.adminInfo.parameters[i]);
                }
            }
        }
    }

</script>
